/*

Table Styled

<table class='table'> will style the table with the generic acc branded coloring and spacing. 

markup:
<table class="table">
  <thead class="thead">
    <tr>
      <th>table header</th>
      <th>table header</th>
      <th>table header</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>table row 1 table cell A
        <br>
        <small class="c_primary">this is small text in blue</small>
      </td>
      <td>table row 1 table cell B
        <br>
        <small class="c_highlight">this is small text with magenta</small>
      </td>
      <td>table row 1 table cell C</td>
    </tr>
    <tr>
      <td>table row 2 table cell A</td>
      <td>table row 2 table cell B</td>
      <td>table row 2 table cell C</td>
    </tr>
  </tbody>
  <tfoot class="tfoot">
    <tr>
      <td colspan="3">footer cell</td>
    </tr>
  </tfoot>
</table>

Styleguide 10.2.1
*/
.table .thead,
.table thead {
  background-color: #f6f6f6;
}
.table .thead th,
.table .thead td,
.table thead th,
.table thead td {
  border-bottom: 2px solid #1faed7;
}
.table .tfoot,
.table tfoot {
  background-color: #f0f0f1;
}
.table .tfoot th,
.table .tfoot td,
.table tfoot th,
.table tfoot td {
  border-top: 1px solid #7c8089;
  font-style: italic;
}

/*

Table Column Shaded

`<table class="table column-shaded">` gives a blue tint to every other column in the table.



markup:
<table class="table column-shaded">
  <thead>
    <tr>
      <th>table header</th>
      <th>table header</th>
      <th>table header</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>row 1 cell A</td>
      <td>row 1 cell B</td>
      <td>row 1 cell C</td>
    </tr>
    <tr>
      <td>row 2 cell A</td>
      <td>row 2 cell B</td>
      <td>row 2 cell C</td>
    </tr>
    <tr>
      <td>row 3 cell A</td>
      <td>row 3 cell B</td>
      <td>row 3 cell C</td>
    </tr>
  </tbody>
  <tfoot>
    <tr>
      <td colspan="3">footer cell</td>
    </tr>
  </tfoot>
</table>

Styleguide 10.2.1.1
*/
.table.column-shaded .thead th:nth-child(even),
.table.column-shaded thead th:nth-child(even) {
  background-color: #d7ecff;
}
.table.column-shaded .tbody td:nth-child(even),
.table.column-shaded tbody td:nth-child(even) {
  background-color: #e4f6fb;
}
.table.column-shaded .tbody tr:nth-child(even) td:nth-child(even),
.table.column-shaded tbody tr:nth-child(even) td:nth-child(even) {
  background-color: #a6d5ff;
}

/*


Table Unshaded

`<table class="table unshaded"> removes the shadding for rows and footer giving a cleaner table. This table type is great for small data sets and removes the 100% wide table that is default in all other tables.  

markup:
<table class="table unshaded">
  <thead>
    <tr>
      <th>table header</th>
      <th>table header</th>
      <th>table header</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>row 1 cell A</td>
      <td>row 1 cell B</td>
      <td>row 1 cell C</td>
    </tr>
    <tr>
      <td>row 2 cell A</td>
      <td>row 2 cell B</td>
      <td>row 2 cell C</td>
    </tr>
    <tr>
      <td>row 3 cell A</td>
      <td>row 3 cell B</td>
      <td>row 3 cell C</td>
    </tr>
  </tbody>
  <tfoot>
    <tr>
      <td colspan="3">footer cell</td>
    </tr>
  </tfoot>
</table>

Styleguide 10.2.1.2
*/
.table.unshaded {
  width: auto;
  border: none;
}
.table.unshaded thead {
  border-top: none;
  border-right: none;
  border-left: none;
}
.table.unshaded tbody tr:nth-child(even) {
  background-color: unset;
}
.table.unshaded tbody tr + tr td {
  border-top: 1px #f0f0f1 solid;
}
.table.unshaded tfoot {
  background-color: unset;
}

