/*
	Shadows

	Shadows can be easily added to container .shadow_{{modifier_class}}  

	shadow_n1			- inset shadow
	shadow_n2			- inset shadow
	shadow_n3			- inset shadow
	shadow_n4			- inset shadow
	shadow_n5			- inset shadow
	shadow_0			- no shadow
	shadow_1			- box shadow
	shadow_2			- box shadow
	shadow_3			- box shadow
	shadow_4			- box shadow
	shadow_5			- box shadow

	markup:
	<div class="{{modifier_class}} p_3 br_solid br_black-1 br_1 c_whtie">container has shadow of {{modifier_class}}</div>

	Styleguide 104.5

 */
.shadow_0 {
