/*
  Hover:Backgrounds

  hovers can easily be added to objects by adding `hover:` or `h:` in front of the background color utility like `h:bg_primary`

  bg_alert              - background color modified light to dark
  bg_primary              - background color modified light to dark
  bg_secondary              - background color modified light to dark
  bg_info              - background color modified light to dark
  bg_highlight              - background color success color
  bg_success              - background color modified light to dark
  bg_warning              - background color modified light to dark

  markup:
  <div class="grid-x m_5">
    <div class="h:{{modifier_class}}-5 cell auto text-center br_1 br_solid br_secondary-4 p_2">5</div>
    <div class="h:{{modifier_class}}-4 cell auto text-center br_1 br_solid br_secondary-4 p_2">4</div>
    <div class="h:{{modifier_class}}-3 cell auto text-center br_1 br_solid br_secondary-4 p_2">3</div>
    <div class="h:{{modifier_class}}-2 cell auto text-center br_1 br_solid br_secondary-4 p_2">2</div>
    <div class="h:{{modifier_class}}-1 cell auto text-center br_1 br_solid br_secondary-4 p_2">1</div>
    <div class="h:{{modifier_class}}-0 cell auto text-center br_1 br_solid br_secondary-4 p_2">0</div>
    <div class="h:{{modifier_class}}-n1 cell auto text-center br_1 br_solid br_secondary-4 p_2 h:c_white">n1</div>
    <div class="h:{{modifier_class}}-n2 cell auto text-center br_1 br_solid br_secondary-4 p_2 h:c_white">n2</div>
    <div class="h:{{modifier_class}}-n3 cell auto text-center br_1 br_solid br_secondary-4 p_2 h:c_white">n3</div>
    <div class="h:{{modifier_class}}-n4 cell auto text-center br_1 br_solid br_secondary-4 p_2 h:c_white">n4</div>
    <div class="h:{{modifier_class}}-n5 cell auto text-center br_1 br_solid br_secondary-4 p_2 h:c_white">n5</div>
  </div>


  Styleguide 109.1

 */
a:focus {
  color: unset;
}

*[class^=" hover:"],
*[class^=" h:"] {
  transition: all ease-out 0.75s;
  cursor: pointer;
}
*[class^=" hover:"]:focus,
*[class^=" h:"]:focus {
  background: inherit;
  border: inherit;
  color: inherit;
}

*[class^="hover:c"],
*[class^=" h:c"] {
  transition: all ease-in 0.5s;
}

