    <div class="h:{{modifier_class}}-n5 cell auto font_bold bg_secondary-3 font_4 font_slab text-center br_1 br_solid br_secondary-4 p_1 ">n5</div>
  </div>


  Styleguide 109.2

 */
.h\:c_white:hover {
